import vouDeVanApi from '../../common/vouDeVanApi';

import qs from 'qs';
import EmpresaFormModel from './EmpresaFormModel';
import IPage, {IPageRequest} from '../../common/IPage';
import EmpresaListagemModel from './EmpresaListagemModel';
import EmpresaAutocompleteModel from "./EmpresaAutocompleteModel";


const EMPRESAS = '/empresas';


export function buscaEmpresasPaginadas(pageable: IPageRequest) {
  const query = qs.stringify(pageable);
  return vouDeVanApi.get<IPage<EmpresaListagemModel>>(EMPRESAS + '?' + query);
}

function removeMascaraCNPJ(cnpj: string) {
  return cnpj.replaceAll('-', '')
    .replaceAll('/', '')
    .replaceAll('.', '');
}

function removeMascaraTelefone(cnpj: string) {
  return cnpj.replaceAll('-', '')
    .replaceAll(' ', '')
    .replaceAll(')', '')
    .replaceAll('(', '');
}

export function cadastraEmpresa(empresa: EmpresaFormModel) {

  empresa.cnpj = removeMascaraCNPJ(empresa.cnpj);
  empresa.telefone = removeMascaraTelefone(empresa.telefone)

  return vouDeVanApi.post<EmpresaFormModel>(EMPRESAS, empresa);
}

export function editaEmpresa(empresaId: number | string, empresa: EmpresaFormModel) {
  empresa.cnpj = removeMascaraCNPJ(empresa.cnpj);
  empresa.telefone = removeMascaraTelefone(empresa.telefone)
  const uri = EMPRESAS + '/' + empresaId;
  return vouDeVanApi.put<null>(uri, empresa);
}

export function deletaEmpresa(empresaId: number | string) {
  const uri = EMPRESAS + '/' + empresaId;
  return vouDeVanApi.delete<null>(uri);
}

export function buscaEmpresaPorId(empresaId: number | string) {
  const uri = EMPRESAS + '/' + empresaId;
  return vouDeVanApi.get<EmpresaFormModel>(uri);
}

export async function buscaEmpresaParaAutocomplete(termo: string) {
  const response = await vouDeVanApi.get<EmpresaAutocompleteModel[]>(EMPRESAS + "/busca-para-autocomplete?termo=" + termo);
  return response.data.map(empresa => {
    return {value: empresa.id, label: `${empresa.id} - ${empresa.nome}`}
  });
}

