import {Form} from 'reactstrap';
import {Formik} from 'formik';
import React from 'react';
import Yup from '../../../common/Yup';
import FormModel from '../../../domain/empresa/FormModel';
import {FormikHelpers} from 'formik/dist/types';
import {AppInputWithLabel} from "../../../components/AppInputWithLabel";
import AppSaveButton from "../../../components/AppSaveButton";


interface Props {
  initialValues: FormModel,
  onSubmit: (values: any, formikHelpers: FormikHelpers<any>) => void | Promise<any>;
}

function EmpresaForm({initialValues, onSubmit}: Props) {
  const validationSchema = Yup.object().shape({
    nome: Yup.string().max(100).required(),
    cnpj: Yup.string().max(18).min(18).required(),
    endereco: Yup.string().max(100).required(),
    representante: Yup.string().max(100).required(),
    telefone: Yup.string().min(14).max(15).required(),
    email: Yup.string().max(150).email().required(),
  });



  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={onSubmit}
    >
      {({handleSubmit, isSubmitting}) => {
        return (
          <Form onSubmit={handleSubmit} autoComplete={'off'}>

            <AppInputWithLabel
              type="text"
              label="Nome"
              id="nome"
            />

            <AppInputWithLabel
              type="text"
              label="CNPJ"
              id="cnpj"
              mask={"99.999.999/9999-99"}
            />

            <AppInputWithLabel
              type="text"
              label="Representante"
              id="representante"
            />

            <AppInputWithLabel
              type="email"
              label="Email"
              id="email"
            />

            <AppInputWithLabel
              type="text"
              label="Telefone"
              id="telefone"
              mask={"(99) 99999-9999"}
            />

            <AppInputWithLabel
              type="text"
              label="Endereço"
              id="endereco"
            />

            <AppSaveButton isSubmitting={isSubmitting}/>
          </Form>
        );
      }}
    </Formik>
  );
}

export default EmpresaForm;
